import './IndustryView.css';
import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';
import TimeLine from '../../components/timeline/timeline';
import LineChartComp from '../../components/lineChart/lineChart';
import DecisionBasisWithGraph from '../../components/decisionBasisWithGraph/decisionBasisWithGraph';
import DecisionBasisWithText from '../../components/decisionBasisWithText/decisionBasisWithText';
import DecisionVoteList from '../../components/decisionVoteList/decisionVoteList';
import EBITChart from '../../components/EBITChart/EBITChart';
import LineChartElPrice from '../../components/lineChartElPrice/lineChartElPrice';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary: {
            light: '#455f8a',
            main: '#2B3B55',
            dark: '#1a2333',
            contrastText: '#DFDBD9',
        },
        // secondary: {
        //     light: '#ff7961',
        //     main: '#f44336',
        //     dark: '#ba000d',
        //     contrastText: '#000',
        // },
    },
});


const IndustryView = (props) => {
    const { sectorName, socket } = props;

    // funktionen som skickar en vote till servern
    const vote = (decisionIndex) => {
        socket.emit("vote", {
            sector: sectorName,
            decisionIndex: decisionIndex
        });
    }

    // Beslut som Industrin kan gra
    const decisions = ["1) Investera i biodrivmedel", "2) Investera i elektrifiering", "3) Investera i R&D", "4) Investera i energieffektivisering"]

    // staten som hller all speldata
    const [gameData, setGameData] = useState(null);

    // Kallas p varje prop update. Begr att f datan frn serven
    useEffect(() => {
        socket.emit("getGameData");
    }, []);

    // Lyssnar p gameData kanalen och sparar datan.
    useEffect(() => {
        socket.on("gameData", gameData => {
            console.log(gameData)
            setGameData(gameData);
        });

        // Sluta lyssna p dismount
        return () => {
            socket.off("gameData");
        }
    })

    // Initialvrde fr turn
    // turn brjar p 1 frn servern men behver att den brjar frn 0 hr fr det anvnds
    // som index drav - 1
    let turn = 0;
    if (gameData && gameData.turn) {
        turn = gameData.turn - 1;
    }

    let mainBody;
    let introText = null;
    if (gameData && gameData.turn === 1) {
        introText = (<p style={{ color: "#484d52" }}>Industrins verksamhet styrs av rrelsemarginalen (rrelseresultat/omsttning) och investeringsbeslut fattas utifrn en internrntekalkyl.
            Rrelsemarginalen kan variera beroende p kostnaden fr:<br />
            <ul>
                <li>el</li>
                <li>utslpp</li>
                <li>investerat kapital</li>
                <li>biodrivmedel</li>
            </ul>
            Internrntekalkylen bestms utifrn marknadspriser p el och biodrivmedel, priset fr att slppa ut CO2 och den initiala investeringskostnaden. I beslutsunerlaget har tre scenarier fr CO2-pris analyserats  att det snks, att det ligger kvar eller att det hjs.
        </p>);
    }
    // Det som ska synas p skrmen nr spelet r i presentatr lge
    if (gameData && (gameData.state === "presenting" && gameData.turn !== 6)) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2 className="centerText">Runda avslutad</h2>
            </div>
        );
        // Det som ska synas nr man r i vanliga spel lget
    } else if (gameData && gameData.state === "playing") {
        mainBody = (
            <div className="wrapper-currentStatus">
                {introText}
                <h2>Nulgesrapport</h2>
                <LineChartComp propData={gameData.data} domain={[0, 1]} dataKey="co2" progKey="co2prog" title="Industrins utslpp (miljoner ton C02-ekvivalenter)" />
                <EBITChart propData={gameData.data} title="Rrelsemarginal (%)" lineColor="#2B3B55" />
                <LineChartElPrice propData={gameData.data} title="Elpriset (kr/kWh)" />
                {/* <PastEvents turn={turn}/> */}
                <h2>Beslutsunderlag</h2>
                <DecisionBasisWithGraph propData={gameData.data[turn].ind_IRR_bio} title={decisions[0]} desc={"Erstt fossilbrnselbaserad industri med tekniker baserat p biodrivmedel. Prognostiserad avkastning fr tre olika scenarier beror p CO2-pris."} />
                <DecisionBasisWithGraph propData={gameData.data[turn].ind_IRR_el} title={decisions[1]} desc={"Elektrifiera fossilbrnselbaserad industri. Prognostiserad avkastning fr tre olika scenarier beror p CO2-pris."} />
                <DecisionBasisWithText title={decisions[2]} text="Satsa p att framtidens teknik r bttre n dagens. Detta alternativ snker investeringsbehoven fr ny teknik kommande simuleringsrundor." />
                <DecisionBasisWithText title={decisions[3]} text="Satsa p att minska din totala energifrbrukning s att dina kostnader och utslpp minskar." />
                <h2>Beslut</h2>
                <h4>Vad vill du gra fr investering?</h4>
                <DecisionVoteList vote={vote} decisions={decisions} disabledOptions={[]} />
            </div>
        );
    } else if (gameData && gameData.turn === 6) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2>Utvrdering</h2>
                <LineChartComp propData={gameData.data} domain={[0, 1]} dataKey="co2" progKey="co2prog" title="Industrins utslpp (miljoner ton C02-ekvivalenter)" />
                <EBITChart propData={gameData.data} title="Rrelsemarginal (%)" lineColor="#2B3B55" />
                <LineChartElPrice propData={gameData.data} title="Elpriset (kr/kWh)" />
            </div>
        );
    }
    return (
        <ThemeProvider theme={theme}>
            <div>
                <TimeLine turns={['2022', '2025', '2030', '2035', '2040', '2045']} turn={turn} sectorName={sectorName} />
                {mainBody}
            </div>
        </ThemeProvider>
    );
}

IndustryView.propTypes = {
    sectorName: PropTypes.string.isRequired,
    socket: PropTypes.object.isRequired
}

export default IndustryView;
