import './ElcoView.css';
import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';
import TimeLine from '../../components/timeline/timeline';
import SupplyDemandGraph from '../../components/supplyDemandGraph/supplyDemandGraph';
import DecisionVoteList from '../../components/decisionVoteList/decisionVoteList';
import EBITChartEl from '../../components/EBITChartEl/EBITChartEl';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary: {
            light: '#BFE5DA',
            main: '#57B998',
            dark: '#3d8f73',
            contrastText: '#FFF',
        },
        // secondary: {
        //     light: '#ff7961',
        //     main: '#f44336',
        //     dark: '#ba000d',
        //     contrastText: '#000',
        // },
    },
});

const ElcoView = (props) => {
    const { sectorName, socket } = props;
    const vote = (decisionIndex, qIndex) => {
        socket.emit("vote", {
            sector: sectorName,
            decisionIndex: decisionIndex,
            question: qIndex,
        });
    }

    // Beslut som Elbolag kan gra
    const decisionList = ["1) ka produktionen", "2) Behll nuvarande produktion", "3) Minska produktionen"];

    // staten som hller all speldata
    const [gameData, setGameData] = useState(null);

    // Kallas p varje prop update. Begr att f datan frn serven
    useEffect(() => {
        socket.emit("getGameData");
    }, []);

    // Lyssnar p gameData kanalen och sparar datan.
    useEffect(() => {
        socket.on("gameData", gameData => {
            console.log(gameData)
            setGameData(gameData);
        });

        // Sluta lyssna p dismount
        return () => {
            socket.off("gameData");
        }
    })

    // Initialvrde fr turn
    // turn brjar p 1 frn servern men behver att den brjar frn 0 hr fr det anvnds
    // som index drav - 1
    let turn = 0;
    if (gameData && gameData.turn) {
        turn = gameData.turn - 1;
    }

    let mainBody;
    let introText = null;
    if (gameData && gameData.turn === 1) {
        introText = (<p style={{ color: "#484d52" }}>Elbolaget reglerar utbudet av el p marknaden och har drmed
        en stor inverkan p elpriset som bestms som en funktion av utbud och efterfrgan.
        Elpris och marknadsbalans pverkar ven verksamhetens lnsamhet.</p>
        );
    }
    // Det som ska synas p skrmen nr spelet r i presentatr lge
    if (gameData && (gameData.state === "presenting" && gameData.turn !== 6)) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2 className="centerText">Runda avslutad</h2>
            </div>
        );
        // Det som ska synas nr man r i vanliga spel lget
    } else if (gameData && gameData.state === "playing") {
        mainBody = (
            <div className="wrapper-currentStatus">
                {introText}
                <h2>Nulgesrapport</h2>
                <SupplyDemandGraph policy={false} propData={gameData.data} domain={[80, 200]} turn={gameData.turn} title="Elmarknaden (TWh)" />
                <EBITChartEl propData={gameData.data} title="Rrelsemarginal (%)" />
                <h2>Beslut</h2>
                <h4>Vad vill du gra med elproduktionen?</h4>
                <DecisionVoteList vote={vote} decisions={decisionList} disabledOptions={[]} />

            </div>
        );
    } else if (gameData && gameData.turn === 6) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2>Utvrdering</h2>
                <SupplyDemandGraph policy={false} propData={gameData.data} domain={[80, 200]} turn={gameData.turn} title="Elmarknaden (TWh)" />
                <EBITChartEl propData={gameData.data} title="Rrelsemarginal (%)" />
            </div>
        );
    }

    return (
        <ThemeProvider theme={theme}>
            <div>
                <TimeLine turns={['2022', '2025', '2030', '2035', '2040', '2045']} turn={turn} sectorName={sectorName} />
                {mainBody}
            </div>
        </ThemeProvider>
    );
}

ElcoView.propTypes = {
    sectorName: PropTypes.string.isRequired,
    socket: PropTypes.object.isRequired
}

export default ElcoView;
