import './Home.css';
import PropTypes from 'prop-types';
import IndustryView from '../IndustryView/IndustryView';
import PolicyView from '../PolicyView/PolicyView';
import ElcoView from '../ElcoView/ElcoView';
import Voters from '../Voters/Voters';
import React, { useState, useEffect } from 'react';

const Home = (props) => {
    const { socket } = props;

    const [sectorIndex, setsectorIndex] = useState(null);

    // Krs varje gng komponenten uppdateras
    useEffect(() => {
        // Skickar till servern att klienten vill ha en sektor
        props.socket.emit("getPlayerSector");
    }, []);


    /// Krs en gng nr komponenten mountas
    useEffect(() => {
        // Lyssnar p playerSector och sparar index vrdet som serven skickar
        props.socket.on("playerSector", index => {
            setsectorIndex(index);
        });

        // Krs p unmount
        // Sger t att sluta lyssna p playerSector kanalen
        return () => {
            props.socket.off("playerSector");
        }
    })

    // Alla spelbara sektorer
    const sectors = [<IndustryView socket={socket} sectorName="Industrin" />, <PolicyView socket={socket} sectorName="Politiker" />, <ElcoView socket={socket} sectorName="Elbolag" />, <Voters socket={socket} sectorName="Vljare" />]

    // En switch som renderar olika saker baserat p index
    switch (sectorIndex) {
        case 0:
            return <main className="industryMain">{sectors[0]}</main>
        case 1:
            return <main className="policyMain">{sectors[1]}</main>
        case 2:
            return <main className="elcoMain">{sectors[2]}</main>
        case 3:
            return <main className="voterMain">{sectors[3]}</main>
        default:
            // Fallback om sectorIndex r ngot annat n 0,1,2,3
            const random = Math.floor(Math.random() * sectors.length);
            return <main>{sectors[random]}</main>
    }

}

Home.propTypes = {
    socket: PropTypes.object.isRequired
}

export default Home;
