import './PolicyView.css';
import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';
import TimeLine from '../../components/timeline/timeline';
import DecisionVoteList from '../../components/decisionVoteList/decisionVoteList';
import SingleLineChart from '../../components/singleLineChart/singleLineChart';
import EBITChart from '../../components/EBITChart/EBITChart';
import TotalEmissionChart from '../../components/totalEmissionChart/totalEmissionChart';
import SupplyDemandGraph from '../../components/supplyDemandGraph/supplyDemandGraph';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary: {
            light: '#f7c6cc',
            main: '#F6B2BB',
            dark: '#d99ca4',
            contrastText: '#F4F4F4',
        },
        // secondary: {
        //     light: '#ff7961',
        //     main: '#f44336',
        //     dark: '#ba000d',
        //     contrastText: '#000',
        // },
    },
});

const PolicyView = (props) => {
    const { sectorName, socket } = props;
    const vote = (decisionIndex, qIndex) => {
        socket.emit("vote", {
            sector: sectorName,
            decisionIndex: decisionIndex,
            question: qIndex,
        });
    }

    // Besluten som Policy kan gra
    const co2dec = ["1) Hj mycket", "2) Hj lite", "3) Behll nuvarande", "4) Snk lite", "5) Snk mycket"];
    const greendec = ["1) Ambitis niv", "2) Mttlig niv", "3) Inga grna subventioner"];
    const svkdec = ["1) Bygg ut mycket", "2) Bygg ut lite", "3) Behll nuvarande"];

    // staten som hller all speldata
    const [gameData, setGameData] = useState(null);

    // Kallas p varje prop update. Begr att f datan frn serven
    useEffect(() => {
        socket.emit("getGameData");
    }, []);

    // Lyssnar p gameData kanalen och sparar datan.
    useEffect(() => {
        socket.on("gameData", gameData => {
            console.log(gameData)
            setGameData(gameData);
        });

        // Sluta lyssna p dismount
        return () => {
            socket.off("gameData");
        }
    })

    // Initialvrde fr turn
    // turn brjar p 1 frn servern men behver att den brjar frn 0 hr fr det anvnds
    // som index drav - 1
    let turn = 0;
    if (gameData && gameData.turn) {
        turn = gameData.turn - 1;
    }

    let mainBody;
    let introText = null;
    if (gameData && gameData.turn === 1) {
        introText = (<p style={{ color: "#484d52" }}>Politikern ansvarar fr:
            <ul>
                <li>att stta ramarna fr industrins investeringsbeslut genom straffskatter p utslpp och investeringsstd genom grna givar.</li>
                <li>att pverka konsumtionsmnster, samma grna giv innefattar ven std till konsumenter fr att kpa elbil.</li>
                <li>verfringsfrmgan i stamntet, vilket fungerar som ett tak fr hur mycket elproduktion som kan komma ut p marknaden.</li>
            </ul>
            Som terkoppling fr politikern ett betyg av vljarna samt en indikation p vilka frgor de prioritierar. Om opinionen blir fr lg begrnsas politikerns handlingsfrihet.
        </p>);
    }
    // Det som ska synas p skrmen nr spelet r i presentatr lge
    if (gameData && (gameData.state === "presenting" && gameData.turn !== 6)) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2 className="centerText">Runda avslutad</h2>
            </div>
        );
        // Det som ska synas nr man r i vanliga spel lget
    } else if (gameData && gameData.state === "playing") {
        const disabledOptionsQ1 = [];
        const disabledOptionsQ2 = [];
        const disabledOptionsQ3 = [];

        if (gameData.data[turn]["voters_rating"] <= 0.3) {
            if (gameData.data[turn].voters_priority_for_policy === 0) {
                disabledOptionsQ1.push(0)
                disabledOptionsQ1.push(1)
                disabledOptionsQ1.push(2)
                disabledOptionsQ2.push(2)
            } else if (gameData.data[turn].voters_priority_for_policy === 1) {
                disabledOptionsQ1.push(0)
                disabledOptionsQ1.push(1)
                disabledOptionsQ1.push(2)
                disabledOptionsQ2.push(0)
                disabledOptionsQ3.push(0)
            } else if (gameData.data[turn].voters_priority_for_policy === 2) {
                disabledOptionsQ1.push(2)
                disabledOptionsQ1.push(3)
                disabledOptionsQ1.push(4)
                disabledOptionsQ2.push(2)
                disabledOptionsQ3.push(1)
                disabledOptionsQ3.push(2)

            }
        }
        mainBody = (
            <div className="wrapper-currentStatus">
                {introText}
                <h2>Nulgesrapport</h2>
                <SingleLineChart tick={true} tickCount={2} propData={gameData.data} domain={[0, 100]} dataKey="voters_rating" title="Vljarnas frtroende fr dig (%)" />
                <TotalEmissionChart propData={gameData.data} domain={[0, 1]} dataKey="totalCo2" progKey="totalCo2prog" title="Sveriges utslpp (miljoner ton C02-ekvivalenter)" />
                <EBITChart propData={gameData.data} title="Industrins rrelsemarginal (%)" lineColor="#F6B2BB" />
                <SupplyDemandGraph policy={true} propData={gameData.data} turn={gameData.turn} domain={[80, 200]} title="Elmarknaden (TWh)" />
                <h2>Beslut</h2>
                <h4>Vad vill du gra med CO2 priset?</h4>
                <DecisionVoteList vote={vote} qIndex={0} decisions={co2dec} disabledOptions={disabledOptionsQ1} />
                <h4>Vlj en niv fr grna subventioner till industrin och konsumenter</h4>
                <DecisionVoteList vote={vote} qIndex={1} decisions={greendec} disabledOptions={disabledOptionsQ2} />
                <h4>Vad vill du gra med stamntet?</h4>
                <DecisionVoteList vote={vote} qIndex={2} decisions={svkdec} disabledOptions={disabledOptionsQ3} />
            </div>
        );
    } else if (gameData && gameData.turn === 6) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2>Utvrdering</h2>
                <SingleLineChart tick={true} tickCount={2} propData={gameData.data} domain={[0, 100]} dataKey="voters_rating" title="Vljarnas frtroende fr dig (%)" />
                <TotalEmissionChart propData={gameData.data} domain={[0, 1]} dataKey="totalCo2" progKey="totalCo2prog" title="Sveriges utslpp (miljoner ton C02-ekvivalenter)" />
                <EBITChart propData={gameData.data} title="Industrins rrelsemarginal (%)" lineColor="#F6B2BB" />
                <SupplyDemandGraph policy={true} propData={gameData.data} turn={gameData.turn} domain={[80, 200]} title="Elmarknaden (TWh)" />
            </div>
        );
    }

    return (
        <ThemeProvider theme={theme}>
            <div>
                <TimeLine turns={['2022', '2025', '2030', '2035', '2040', '2045']} turn={turn} sectorName={sectorName} />
                {mainBody}
            </div>
        </ThemeProvider>
    );
}

PolicyView.propTypes = {
    sectorName: PropTypes.string.isRequired,
    socket: PropTypes.object.isRequired
}

export default PolicyView;
