import './Voters.css';
import PropTypes from 'prop-types';
import React, { useState, useEffect } from 'react';
import TimeLine from '../../components/timeline/timeline';
import DecisionVoteList from '../../components/decisionVoteList/decisionVoteList';
import EBITChart from '../../components/EBITChart/EBITChart';
import SingleLineChart from '../../components/singleLineChart/singleLineChart';
import TotalEmissionChart from '../../components/totalEmissionChart/totalEmissionChart';
import CarCosts from '../../components/carCosts/carCosts';
import VoterCosts from '../../components/votersCosts/votersCosts';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary: {
            light: '#f7c6cc',
            main: '#F6B2BB',
            dark: '#d99ca4',
            contrastText: '#2C3955',
        },
        // secondary: {
        //     light: '#ff7961',
        //     main: '#f44336',
        //     dark: '#ba000d',
        //     contrastText: '#000',
        // },
    },
});

const Voters = (props) => {
    // funktionen som skickar en vote till servern
    const vote = (decisionIndex, qIndex) => {
        props.socket.emit("vote", {
            sector: props.sectorName,
            decisionIndex: decisionIndex,
            question: qIndex,
        });
    }

    // Beslut som Vljare kan gra
    const ratingDec = ["1) Mycket bra", "2) Bra", "3) Dligt", "4) Mycket dligt"];
    const carDec = ["1) Elbil", "2) Brnslebil"];
    const mostImpQuest = ["1) Arbetslsheten", "2) Din disponibla inkomst", "3) CO2-utslppen"];

    // staten som hller all speldata
    const [gameData, setGameData] = useState(null);

    // Kallas p varje prop update. Begr att f datan frn serven
    useEffect(() => {
        props.socket.emit("getGameData");
    }, []);

    // Lyssnar p gameData kanalen och sparar datan.   
    useEffect(() => {
        props.socket.on("gameData", gameData => {
            console.log(gameData)
            setGameData(gameData);
        });

        // Sluta lyssna p dismount
        return () => {
            props.socket.off("gameData");
        }
    })

    // Initialvrde fr turn
    // turn brjar p 1 frn servern men behver att den brjar frn 0 hr fr det anvnds
    // som index drav - 1
    let turn = 0;
    if (gameData && gameData.turn) {
        turn = gameData.turn - 1;
    }

    let mainBody;
    let introText = null;
    if (gameData && gameData.turn === 1) {
        introText = (<p style={{ color: "#c1c2c3" }}>Vljaren har som uppgift att ge politikern input om hur vl denne gr sitt jobb. Vljaren fr en verblick ver utslppen och arbetslsheten. Den fr ven en bild av sin egen ekonomi. Denna bestms av hur det gr fr industrin/arbetslsheten och hur dyrt det r att leva fr nrvarande. Detta senare bestms av tv faktorer: Priset fr att slppa ut samt hur mycket ekonomin slpper ut. Vljaren fr ocks en kalkyl ver vad det i dagslget kostar att ha en elbil eller en bensinbil. Utifrn detta ska den fatta ett beslut om vilket av alternativen den hade valt. Detta kommer i sin tur pverka hur stora utslppen blir frn transportsektorn.
        </p>);
        introText = (<p style={{ color: "#c1c2c3" }}>Vljaren har som uppgift att ge politikerna terkoppling. De viktigaste frgorna vi valt att belysa r:
            <ul>
                <li>Sveriges totala CO2 utslpp och hur vl vi uppnr satta ml</li>
                <li>arbetslsheten i samhllet i stort</li>
                <li>den egna ekonomin</li>
            </ul>
            Vljaren har mjlighet att rsta p hur bra jobb politikerna gr och ven indikera
            vilka frgor som de anser kritiska. Som beslutsunderlag fr eventuell uppgradering till
            elbil, vilket pverkar bde privata utslpp och privatekonomin, presenteras en kalkyl ver vad
            de olika fordonsalternativen kostar ur ett livscykelperspektiv.
        </p>);
    }
    // Det som ska synas p skrmen nr spelet r i presentatr lge
    if (gameData && (gameData.state === "presenting" && gameData.turn !== 6)) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2 className="centerText">Runda avslutad</h2>
            </div>
        );
        // Det som ska synas nr man r i vanliga spel lget
    } else if (gameData && gameData.state === "playing") {
        mainBody = (
            <div className="wrapper-currentStatus">
                {introText}
                <h2>Nulgesrapport</h2>
                <TotalEmissionChart propData={gameData.data} domain={[0, 1]} dataKey="totalCo2" progKey="totalCo2prog" title="Sveriges utslpp (miljoner ton C02-ekvivalenter)" />
                {/* <EBITChart propData={gameData.data} title="Industrins EBIT-margin (%)" lineColor="#F6B2BB" /> */}
                <SingleLineChart tick={true} tickCount={6} propData={gameData.data} domain={[0, 25]} dataKey="voter_unemployment" title="Arbetslshet (%)" />
                <VoterCosts propData={gameData.data} title="Din ekonomi (tKr)" />
                <CarCosts propData={gameData.data} title="Fordonskostnad (tKr)" />
                <h2>Beslut</h2>
                <h4>Hur bra skter politkerna sitt jobb?</h4>
                <DecisionVoteList vote={vote} qIndex={0} decisions={ratingDec} disabledOptions={[]} />
                <h4>Vilken typ av bil hade du kpt i nulget?</h4>
                <DecisionVoteList vote={vote} qIndex={1} decisions={carDec} disabledOptions={[]} />
                <h4>Vilken r din viktigaste frga?</h4>
                <DecisionVoteList vote={vote} qIndex={2} decisions={mostImpQuest} disabledOptions={[]} />
            </div>
        );
    } else if (gameData && gameData.turn === 6) {
        mainBody = (
            <div className="wrapper-currentStatus">
                <h2>Utvrdering</h2>
                <TotalEmissionChart propData={gameData.data} domain={[0, 1]} dataKey="totalCo2" progKey="totalCo2prog" title="Sveriges utslpp (miljoner ton C02-ekvivalenter)" />
                {/* <EBITChart propData={gameData.data} title="Industrins EBIT-margin (%)" lineColor="#F6B2BB" /> */}
                <SingleLineChart tick={true} tickCount={6} propData={gameData.data} domain={[0, 25]} dataKey="voter_unemployment" title="Arbetslshet (%)" />
                <VoterCosts propData={gameData.data} title="Din ekonomi (tKr)" />
                <CarCosts propData={gameData.data} title="Fordonskostnad (tKr)" />
            </div>
        );
    }

    return (
        <ThemeProvider theme={theme}>
            <div>
                <TimeLine turns={['2022', '2025', '2030', '2035', '2040', '2045']} turn={turn} sectorName={props.sectorName} />
                {mainBody}
            </div>
        </ThemeProvider>
    );
}

Voters.propTypes = {
    sectorName: PropTypes.string.isRequired,
    socket: PropTypes.object.isRequired
}

export default Voters;
